import httpUtils from 'config/index'

class articleService {
  // 登录接口

  getJoin() {
    return httpUtils.get(`/index/article/information`)
  }
  getIndustry(){
    return httpUtils.get("/index/article/presentation");
  }

  /**
   * 获取文章列表
   */
  getArticleList(params){

    return httpUtils.get("/index/article/article_list",params);
  }
}
export default new articleService()



