<template>
  <div>
    <Search></Search>
    <div class="commonNav">
      <div class="wrapper">
        <div class="part">
          <a href="">
            <span>首页</span>
            <i></i>
          </a>
          <router-link to="/list">
            <span>找项目</span>
            <img class="hot" src="../../assets/hot.png">
            <i></i>
          </router-link>
          <router-link to="/rankingList">
            <span>排行榜</span>
            <i></i>
          </router-link>
          <router-link class="active" to="/news">
            <span>行业资讯</span>
            <i></i>
          </router-link>
        </div>
      </div>
    </div>
    <div class="greyBg">
      <div class="wrapper clearfix">
        <p class="adress">您的位置：行业资讯 > 加盟资讯</p>
        <div class="listLeft">
          <ul class="listNav clearfix" @click="tabChange">
            <li :class="[type == 1 ? 'active':'']" data-type="1">加盟资讯<i></i></li>
            <li :class="[type == 2 ? 'active':'']" data-type="2">加盟攻略<i></i></li>
            <li :class="[type == 0 ? 'active':'']" data-type="0">行业报告<i></i></li>
          </ul>
          <div >
            <dl class="listDL clearfix" v-for="item in data" :key="'article'+item.id">
              <router-link :to="'/consultDetail?id='+item.id">
                <dt>
                  <img :src="item.img">
                </dt>
                <dd>
                  <h3>{{item.title}}</h3>
                  <p class="desc">{{item.sign}}</p>
                  <p class="icon">
                    <span><i class="iconfont icon-data"></i>{{item.addtime}}</span>
                    <span><i class="iconfont icon-look"></i>{{item.read_num}}</span>
                  </p>
                </dd>
              </router-link>
            </dl>
          </div>
          <el-pagination class="paging" background layout="prev, pager, next" :total="count" @current-change="pageChange"></el-pagination>
        </div>
        <div class="listRight">
          <HotOrder :more="false"></HotOrder>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
  import Search from '@/components/Search'
  import HotOrder from '@/components/HotOrder'
  import api from '@/api/article-api'
  export default {
    name: "index.vue",
    data(){
      return{
        type: 1, //0行业报告1加盟资讯2加盟攻略
        page: 0,
        data:[],
        count: 0
      }
    },
    mounted(){
        const {type =1} = this.$route.query;
        this.type = type;
        this.getData()
    },
    methods:{
      /**
       * 切换文章类型
       */
      tabChange:function(e){
        this.type = e.target.dataset.type;
        this.page = 0;
        this.getData();

      },
      getData: function(){
        const {page,type} = this;
        api.getArticleList({page,type}).then(data => {
            this.count = data.count;
            this.data= data.data;
        })
      },
      pageChange:function(e){
        this.page = e-1;
        window.scrollTo(0,0);
        this.getData();
      }
    },
    components: {
      Search,
      HotOrder
    }
  }
</script>

<style lang="less" scoped type="text/less">
  .commonNav{
    width:100%;
    height: 40px;
    line-height: 40px;
    background: #2F333B;
    font-size: 16px;
    color: #fff;
    .all{
      float: left;
      width: 240px;
      height: 40px;
      text-align: center;
      background: #51575D;
      .icon-mulu{
        margin-right: 12px;
      }
      img,label{
        display: inline-block;
        vertical-align: middle;
      }
      img{
        margin:-2px 14px 0 0;
      }
    }
    .part{
      a{
        float: left;
        width: 160px;
        height: 40px;
        position: relative;
        color: #fff;
        text-align: center;
        .hot{
          position: absolute;
          top: 4px;
          margin-left: 6px;
        }
      }
      a.active{
        background: #1A1D21;
        i{
          width: 34px;
          height: 2px;
          background: -webkit-gradient(linear, 100% 0, 0 0, from(#579EFA), to(#4F74F4));
          background: -webkit-linear-gradient(to bottom, #579EFA, #4F74F4);
          background: -moz-linear-gradient(to bottom, #579EFA, #4F74F4);
          background: -o-linear-gradient(to bottom, #579EFA, #4F74F4);
          background: linear-gradient(to bottom, #579EFA, #4F74F4);
          position: absolute;
          left: 50%;
          margin-left: -17px;
          bottom: 2px;
          z-index: 1;
        }
      }
    }
    .issueBtn{
      float: right;
      color: #FEAB17;
    }
  }
  .greyBg{
    padding: 0 0 100px;
    background: #F4F5FA;
    .adress{
      color: #999;
      height: 60px;
      line-height: 60px;
    }
    .listLeft{
      float: left;
      width: 820px;
      padding: 0 41px 35px 34px;
      background: #fff;
      .listNav{
        height: 62px;
        line-height: 62px;
        border-bottom: 1px solid #E3E4E5;
        li{
          float: left;
          margin-right: 40px;
          position: relative;
          cursor: pointer;
        }
        li.active{
          color: #000;
          i{
            width:20px;
            height:3px;
            background:linear-gradient(0deg,rgba(79,116,244,1) 0%,rgba(87,158,250,1) 100%);
            border-radius:2px;
            position: absolute;
            left: 50%;
            margin-left: -10px;
            bottom: 9px;
          }
        }
      }
      .listDL{
        padding: 20px 0;
        border-bottom: 1px solid #E3E4E5;
        dt{
          float: left;
          width: 198px;
          height: 140px;
          img{
            display: block;
            width: 198px;
            height: 140px;
          }
        }
        dd{
          float: left;
          width: 580px;
          margin-left: 25px;
          h3{
            font-size: 18px;
            color: #000;
            margin-top: 12px;
          }
          .desc{
            line-height: 26px;
            overflow: hidden;
            text-overflow: ellipsis;
            display: -webkit-box;
            -webkit-line-clamp: 2;
            -webkit-box-orient: vertical;
            margin-top: 13px;
          }
          .icon{
            color: #999;
            line-height: 20px;
            margin-top: 12px;
            span{
              margin-right: 35px;
              i{
                color: #ccc;
                margin-right: 10px;
              }
            }

          }
        }
      }
    }
    .listRight{
      float: right;
      width: 295px;
    }
  }
  .paging{
    margin: 30px;
    text-align: center;
  }
</style>



